import {Injectable} from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class UtilityService {
  public getViewPortHeight(): number {
    return Math.max(document.documentElement.clientWidth, window.innerWidth || 0);
  }

  public getViewPortWidth(): number {
    return Math.max(document.documentElement.clientWidth, window.innerWidth || 0);
  }
}
